/*  Animation to trigger ovule apparition on the 14th day */
.appearOvule{
  -webkit-animation: appearOvule 3s forwards;
          animation: appearOvule 3s forwards;
}

.notAppearOvule{
  display: none;
}
.growPituitary{
  -webkit-transform: scale(1.4);
          transform: scale(1.4);
  -webkit-transform-origin: 50% 15%;
          transform-origin: 50% 15%;
  transition: all .05s ease-in;
}
@-webkit-keyframes appearOvule {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(-10%, -5%);
            transform: translate(-10%, -5%);
  }
}
@keyframes appearOvule {
  0% {
    opacity: 0;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(-10%, -5%);
            transform: translate(-10%, -5%);
  }
}
.deleteOvaries{
  display: none;
}
.deletePituitary{
  display: none;
}

